"use client";

// React and Next
import { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";
import Link from "next/link";

//API
import {
  AddWishListApi,
  WishListDeleteApi,
  productCompareApi,
} from "@/api/clientsideaction/productsearch";

//3rd Party
import StarRatings from "react-star-ratings";
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";

//Utils
import { imageUrl } from "@/utilities/url";
import { GetAddonList, GetCompareId } from "@/utilities/localStorage";
import { PriceCalculate, PriceTaxCalculate } from "@/utilities/PriceCalculate";

//Component
import ToastMessage from "@/Components/ToastMessage";
import PaginationComp from "@/Components/Pagination";
import ProductListSkeleton from "./skeleton";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

export default function Productlist({
  productdetails,
  loader,
  filterdetail,
  selectedval,
  headfil,
  handleRemove,
  params,
  pagecount,
  nPages1,
  currentPage,
  onPageChange,
  token,
}) {
  const { t } = useTranslation();
  const router = useRouter();
    const addonlist = GetAddonList();

  const productname = useSelector((state) => state.userReducer.categoryname);
  const wishCount = useSelector((state) => state.userReducer.wishCount);
  const [comparetrigger, setCompareTrigger] = useState(0);
  const [trigger, setTrigger] = useState(0);

  const dispatch = useDispatch();
  const productIds = GetCompareId();

  const handleWishiList = (data) => {
    if (token) {
      productdetails?.map((product) => {
        if (data.productId == product.productId) {
          product.wishListStatus = product.wishListStatus == 0 ? 1 : 0;
        }
      });

      if (data?.wishListStatus == 0) {
        WishListDeleteApi(data.productId, dispatch, wishCount);
      } else {
        AddWishListApi(data.productId, dispatch, wishCount);
      }
    } else {
      router.push("/buyer-login");
    }
    setTrigger(trigger + 1);
  };

  const handleAddItemToCompare = (e, productId) => {
    let data = 0;
    if (productIds !== null && productIds !== undefined) {
      var productIdIndex = productIds?.indexOf(productId);
      if (productIdIndex !== -1) {
        let localCompareId = GetCompareId();
        localCompareId.splice(productIdIndex, 1);
        localStorage.setItem("compareId", JSON.stringify(localCompareId));
        setCompareTrigger(comparetrigger + 1);
        ToastMessage({
          type: "success",
          message: t("toastMessage.compareProductRemove"),
        });
      } else {
        const compareIds = [...productIds, productId];
        if (compareIds && compareIds.length > 2) {
          ToastMessage({
            type: "error",
            message: t("toastMessage.compareProductLimit"),
          });
        } else {
          productCompareApi(
            data,
            compareIds,
            setCompareTrigger,
            comparetrigger,
            productIds
          );
        }
      }
    } else {
      let productIds = [];
      productIds.push(productId);
      productCompareApi(data, productIds, setCompareTrigger, comparetrigger);
    }
  };

  function compareCheckFunction(productId) {
    let productIds = JSON.parse(localStorage.getItem("compareId"));

    if (productIds && productIds.length !== 0) {
      let compareCheck = productIds.some((value) => value === productId);
      return compareCheck;
    }
  }

  return (
    <div className="w-full px-0 sm:px-0">
      <div className="flex flex-wrap gap-4 gap-y-1">
        {filterdetail &&
          filterdetail?.map(
            (data, ind) =>
              headfil &&
              headfil?.[ind] == true && (
                <div className="flex items-center mb-4 space-x-2" key={ind}>
                  <h3 className="text-xs font-normal text-black-300 whitespace-nowrap">
                    {data?.sectionName} :
                  </h3>
                  <div className="flex flex-wrap items-center space-x-2">
                    {data &&
                      data?.sectionItem &&
                      data?.sectionItem?.map(
                        (item, index) =>
                          selectedval &&
                          selectedval?.includes(item?.id) && (
                            <div
                              className="flex items-center px-2 py-1 space-x-2 rounded bg-grey-300"
                              key={index}
                            >
                              <p className="text-xs font-normal text-darkblack">
                                {item?.itemName}
                              </p>
                              <a
                                role="button"
                                className="cursor-pointer"
                                onClick={(e) =>
                                  handleRemove(
                                    item?.id,
                                    data?.sectionName + "~" + item?.itemName,
                                    data?.sectionType
                                  )
                                }
                              >
                                <img
                                  src="/img/close-dark.svg"
                                  alt="close-dark"
                                />
                              </a>
                            </div>
                          )
                      )}
                  </div>
                </div>
              )
          )}
      </div>

      {loader ? (
        productdetails && productdetails?.length > 0 ? (
          <div className="gap-x-4 gap-y-5 grid grid-cols-[repeat(auto-fill,minmax(264px,1fr))]">
            {productdetails &&
              productdetails?.map((data, index) => (
                <Fragment key={index}>
                  {/* Compare route button */}

                  {productIds && productIds?.length > 0 && (
                    <Link
                      href={"/product-compare"}
                      className={
                        "inline-flex cursor-pointer justify-center uppercase font-medium items-center rounded-md bg-[var(--color)] text-white text-base gap-x-4 py-4 px-8 fixed bottom-11 right-11 z-10 w-36 h-10"
                      }
                    >
                      {t("compare.Compare")}
                      <span className="flex items-center justify-center bg-green-slate min-w-2">
                        {productIds?.length}
                      </span>
                    </Link>
                  )}

                  {/* listcard */}

                  <div
                    className="group group-item block relative hover:shadow-[0px_0px_10px_#00000014] rounded-[5px] overflow-hidden"
                    key={index}
                  >
                    {/* Wishlist and Compare button */}
                    <div className="top-2 right-2 rtl:right-[unset] rtl:left-2 z-[5] absolute flex items-center space-x-3">
                      {/* Compare button */}
                      <div
                        onClick={(e) =>
                          handleAddItemToCompare(e, data?.productId)
                        }
                        className={
                          compareCheckFunction(data?.productId)
                            ? "cursor-pointer   rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3"
                            : `item cursor-pointer   rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3`
                        }
                      >
                        {compareCheckFunction(data?.productId) ? (
                          <img
                            src="/img/compare-outline-active.svg"
                            alt="compare-outline-active"
                            className="w-5"
                          />
                        ) : (
                          <img
                            src="/img/compare-outline.svg"
                            alt="compare-outline"
                            className="w-5"
                          />
                        )}
                        <span className="shadow-md tooltip-text">
                          {t("tooltip.compare")}
                        </span>
                      </div>
                      {/* Wishlist button */}
                      <div
                        onClick={() => handleWishiList(data)}
                        className={
                          data.wishListStatus == 0
                            ? "item cursor-pointer   z-20 rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3"
                            : `cursor-pointer  rounded-full flex justify-center items-center shadow-md h-10 w-10 text-2xl bg-white tooltip3`
                        }
                      >
                        {data.wishListStatus == 0 ? (
                          <img
                            src="/img/heart-outline-wisit.svg"
                            alt="heart-outline-wisit"
                            className="w-5"
                          />
                        ) : (
                          <img
                            src="/img/heart-select-red-wisit.svg"
                            alt="heart-select-red-wisit"
                            className="w-5"
                          />
                        )}

                        <span className="shadow-md tooltip-text">
                          {t("tooltip.wishList")}
                        </span>
                      </div>
                    </div>

                    <Link
                      prefetch
                      href={`/products/${params?.slug}/${data?.productSlug}`}
                    >
                      {/* Product Image */}
                      <div className="bg-[#f8f8f8] h-[228px]">
                        <img
                          className="object-contain w-full h-full transition-transform duration-300 group-hover:scale-105"
                          src={
                            imageUrl +
                            "?path=" +
                            data?.containerName +
                            "&name=" +
                            data?.image +
                            "&width=182&height=182"
                          }
                          width={182}
                          height={182}
                          alt={data?.name}
                          onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src = "/img/no-image.png";
                            currentTarget.alt = "no-image";
                          }}
                        />
                      </div>

                      {/* Product Title, Price, Rating */}
                      <div className="p-[12px]">
                        <span className="mb-[10px] min-h-[40px] font-normal text-[#8B8BA3] text-[14px] hover:underline line-clamp-2 leading-[20px]">
                          {data?.productNameTrans
                            ? data?.productNameTrans
                            : data?.name}
                        </span>
                        <div className="flex flex-wrap items-center gap-[8px] mb-[10px]">
                          <p className="font-[900] text-[#333333] text-[18px] leading-[22px] whitespace-nowrap">
                            <CustomCurrencyFormatter
                              price={PriceCalculate(
                                data?.price,
                                data?.pricerefer,
                                data?.flag,
                                data?.taxType,
                                data?.taxValue
                              )}
                            />
                          </p>
                          {data?.flag == 1 && (
                            <>
                              <p className="font-[600] text-[#8B8BA3] text-[14px] line-through leading-[17px] whitespace-nowrap">
                                {t("productdetails.Mrp")}{" "}
                                <CustomCurrencyFormatter
                                  price={PriceTaxCalculate(
                                    data?.price,
                                    data?.taxType,
                                    data?.taxValue
                                  )}
                                />
                              </p>
                              <p className="font-[600] text-[12px] text-[var(--color)] leading-[15px] whitespace-nowrap">
                                {Math.round(
                                  ((PriceTaxCalculate(
                                    data?.price,
                                    data?.taxType,
                                    data?.taxValue
                                  ) -
                                    PriceTaxCalculate(
                                      data?.pricerefer,
                                      data?.taxType,
                                      data?.taxValue
                                    )) /
                                    PriceTaxCalculate(
                                      data?.price,
                                      data?.taxType,
                                      data?.taxValue
                                    )) *
                                    100
                                )}
                                {t("productdetails.OFF")}
                              </p>
                            </>
                          )}
                        </div>
                        {addonlist &&
                          addonlist?.["rating-review"] &&
                          addonlist?.["rating-review"]?.status == 1 &&
                          data?.ratingCount > 0 &&
                          data?.reviewCount > 0 && (
                            <div className="flex items-center gap-[5px]">
                              <StarRatings
                                rating={
                                  data?.ratingCount &&
                                  parseFloat(data?.ratingCount)
                                }
                                starRatedColor="black"
                                starDimension="13px"
                                starSpacing="3px"
                                numberOfStars={5}
                                name="rating"
                                svgIconPath="M12.489,5.03a.665.665,0,0,0-.572-.457L8.3,4.245,6.873.9A.666.666,0,0,0,5.649.9L4.22,4.245.6,4.573A.666.666,0,0,0,.227,5.738l2.732,2.4-.806,3.548a.665.665,0,0,0,.99.72l3.117-1.864L9.377,12.4a.666.666,0,0,0,.991-.72L9.562,8.134l2.732-2.4A.666.666,0,0,0,12.489,5.03ZM6.321,10.5"
                                svgIconViewBox="0 0 12.522 12"
                              />
                              <p className="mt-1 font-normal text-[#8B8BA3] text-[12px] leading-[20px]">
                                {`(${data?.reviewCount} ${
                                  data?.reviewCount <= 1
                                    ? t("productcontent.review")
                                    : t("productcontent.reviews")
                                })`}
                              </p>
                            </div>
                          )}
                      </div>
                    </Link>
                  </div>
                </Fragment>
              ))}
          </div>
        ) : (
          <div className="mx-auto mt-32 min-h-[40vh]">
            <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
              <img src="/img/no-data.svg" alt="no-data" />
              <div>
                <h5 className="mb-1 text-xl font-black text-black-300">
                  {t("myprofile.NoResults")}{" "}
                  <span className="text-green-500">
                    &quot;
                    {productname?.[0].charAt(0).toUpperCase() +
                      productname?.[0].slice(1).toLowerCase()}
                    &quot;
                  </span>
                </h5>
              </div>
            </div>
          </div>
        )
      ) : (
        <ProductListSkeleton />
      )}

      <div className="relative flex flex-col w-full mt-4 sm:flex-row">
        {pagecount >= 10 && (
          <>
            <div className="absolute left-0 hidden sm:block top-4">
              {t("productlist.pages")} {currentPage} {t("productlist.of")}{" "}
              {nPages1}
            </div>

            <PaginationComp
              nPages1={nPages1}
              currentPage={currentPage}
              onPageChange={onPageChange}
            />
          </>
        )}
      </div>
    </div>
  );
}
