"use client";

// React and Next
import { useEffect, useState } from "react";

// Redux
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import {
  setAttributeFilter,
  setSelectedFilterIds,
  setVariantFilter,
} from "@/redux/slices/variant";

// API
import {
  Productfilters,
  ProductfiltersCount,
  customfiltersMaxCount,
} from "@/api/clientsideaction/productsearch";
import { categoryname, offsetCount } from "@/redux/slices/userSlice";

// Components
import Breadcrumb from "../Breadcrumbs";
import Productfilter from "./Productfilters";
import Productlist from "./Productlist";
import Contactus from "../Contactus";
import { useTranslation } from "react-i18next";

export default function Productlistview({
  token,
  params,
  pagecount,
  filter,
  selectedCategory,
  categoryList,
}) {
  const [productdetails, setProductdetails] = useState([]);
  const [loader, setLoader] = useState(false);
  const [filterdetail, setFilterdetails] = useState();
  const [selectedval, setSelectedval] = useState([]);
  const [fildata, setFildata] = useState([]);
  const [headfil, setHeadfil] = useState([]);
  const dispatch = useDispatch();
  const offset = useSelector((state) => state.userReducer.offsetVal);
  const [loaders, setLoaders] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [trigger, setTriiger] = useState(0);
  const [sortname, setSortname] = useState("All");
  const [order, setorder] = useState();
  const [triggers, setTriggers] = useState(0);
  const [mPrices, setMprice] = useState("");
  const [childCategory, setChildCategory] = useState();
  // Any Price
  const [anyPrice, setAnyPrice] = useState(true);
  const [customPrice, setCustomPrice] = useState(false);
  const [customLowPrice, setCustomLowPrice] = useState("");
  const [customHighPrice, setCustomHighPrice] = useState("");
  // Pagination Total Count
  const [totCount, setTotConunt] = useState(pagecount?.data);
  const { t } = useTranslation();

  const selectedfilter = useSelector((s) => s.VariantRedux.selectedFilterIds);
  const variantfilter = useSelector((s) => s.VariantRedux.variantFilter);
  const attibutefilter = useSelector((s) => s.VariantRedux.attributeFilter);

  useEffect(() => {
    let array = [];
    for (let index = 0; index < fildata?.length; index++) {
      if (fildata?.[index].some((id) => selectedfilter?.includes(id))) {
        array.push(true);
      } else {
        array.push(false);
      }
      setHeadfil(array);
    }
    setSelectedval(selectedfilter);
  }, [selectedfilter]);

  useEffect(() => {
    if (customLowPrice !== "" || customHighPrice !== "") {
      let timer = setTimeout(() => {
        Productfilters(
          params?.slug,
          attibutefilter,
          setProductdetails,
          setLoader,
          offset,
          variantfilter,
          order,
          customHighPrice == "" ? mPrices : customHighPrice,
          customLowPrice,
          dispatch,
          categoryname
        );
        ProductfiltersCount(
          params?.slug,
          attibutefilter,
          setTotConunt,
          variantfilter,
          order,
          customHighPrice == "" ? mPrices : customHighPrice,
          customLowPrice
        );
      }, 500);

      return () => {
        clearTimeout(timer);
      };
    }
  }, [customLowPrice, customHighPrice]);

  useEffect(() => {
    setLoader(false);
    Productfilters(
      params?.slug,
      attibutefilter,
      setProductdetails,
      setLoader,
      offset,
      variantfilter,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice,
      dispatch,
      categoryname
    );
    ProductfiltersCount(
      params?.slug,
      attibutefilter,
      setTotConunt,
      variantfilter,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  }, [attibutefilter, variantfilter, trigger]);

  useEffect(() => {
    dispatch(setAttributeFilter([]));
    dispatch(setSelectedFilterIds([]));
    dispatch(setVariantFilter([]));
    customfiltersMaxCount(setMprice);
    handlefilter();
    Productfilters(
      params?.slug,
      [],
      setProductdetails,
      setLoader,
      offset,
      [],
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice,
      dispatch,
      categoryname
    );
    ProductfiltersCount(
      params?.slug,
      [],
      setTotConunt,
      [],
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
  }, []);

  const handlefilter = async () => {
    if (filter?.status == 1) {
      setLoaders(true);

      setFilterdetails(filter?.data);
      let array = [];
      filter?.data?.map((filters) => {
        let obj = [];
        filters?.sectionItem?.map((item, index) => {
          obj.push(item.id);
        });
        array.push(obj);
      });
      setFildata(array);
    }
  };

  const handleRemove = (id, fullId, type) => {
    setLoader(false);
    if (selectedfilter?.indexOf(id) !== -1) {
      dispatch(setSelectedFilterIds(selectedval?.filter((ind) => ind != id)));
    } else {
      dispatch(setSelectedFilterIds([...selectedval, id]));
    }

    let manuSubArr = [...attibutefilter];

    let variantmenuSubArr = [...variantfilter];
    if (type == 2) {
      if (manuSubArr?.indexOf(fullId) !== -1) {
        manuSubArr = manuSubArr?.filter((manufactId) => manufactId != fullId);
      } else {
        manuSubArr.push(fullId);
      }
    } else if (type == 1) {
      if (variantmenuSubArr?.indexOf(fullId) !== -1) {
        variantmenuSubArr = variantmenuSubArr?.filter(
          (manufactId) => manufactId != fullId
        );
      } else {
        variantmenuSubArr.push(fullId);
      }
    }

    dispatch(setAttributeFilter(manuSubArr));
    dispatch(setVariantFilter(variantmenuSubArr));

    Productfilters(
      params?.slug,
      manuSubArr,
      setProductdetails,
      setLoader,
      0,
      variantmenuSubArr,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice,
      dispatch,
      categoryname
    );
    ProductfiltersCount(
      params?.slug,
      manuSubArr,
      setTotConunt,
      variantmenuSubArr,
      order,
      customHighPrice == "" ? mPrices : customHighPrice,
      customLowPrice
    );
    dispatch(offsetCount(0));
    // setOffset(0)
  };

  const handleReset = () => {
    let reObj = {
      price: "",
      latestArrival: "",
    };
    let sortObj = {
      id: 1,
      name: "Lowest Price",
      number: 0,
    };
    setSortname(sortObj.name);
    setLoader(false);
    setSelectedval([]);
    setAnyPrice(true);
    setCustomPrice(false);
    setCustomLowPrice("");
    setCustomHighPrice("");
    setCustomLowPrice("");
    setCustomHighPrice("");
    dispatch(offsetCount(0));
    // setOffset(0)
    Productfilters(
      params?.slug,
      "",
      setProductdetails,
      setLoader,
      0,
      "",
      reObj,
      mPrices,
      "",
      dispatch,
      categoryname
    );
    ProductfiltersCount(params?.slug, "", setTotConunt, "", reObj, mPrices, "");
    dispatch(setAttributeFilter([]));
    dispatch(setVariantFilter([]));
    dispatch(setSelectedFilterIds([]));
  };

  useEffect(() => {
    if (categoryList?.length > 0 && selectedCategory?.parentInt !== 0) {
      categoryList?.map((category) => {
        const child = category?.children?.find(
          (childCategories) =>
            childCategories?.categoryId == selectedCategory?.parentInt
        );
        if (child) {
          const parentCategory = categoryList?.find(
            (category) => category?.categoryId == child?.parentInt
          );
          setChildCategory(parentCategory);
        }
      });
    }
  }, [selectedCategory, categoryList]);

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: selectedCategory?.categoryNameTrans
        ? selectedCategory?.categoryNameTrans
        : selectedCategory?.name,
      url: "javascript:void(0)",
    },
  ];

  const parentCategory = categoryList?.find((cat) =>
    cat?.children?.some((child) => child?.name === selectedCategory?.name)
  );

  const breadCrumbs = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: parentCategory?.categoryNameTrans
        ? parentCategory?.categoryNameTrans
        : parentCategory?.name,
      url: `/product/${parentCategory?.categorySlug}`,
    },
    {
      text: selectedCategory?.categoryNameTrans
        ? selectedCategory?.categoryNameTrans
        : selectedCategory?.name,
      url: "javascript:void(0)",
    },
  ];

  const matchingChild = childCategory?.children?.find((child) =>
    child?.children?.some((sub) => sub?.name === selectedCategory?.name)
  );

  const subbreadCrumbs = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: childCategory?.categoryNameTrans
        ? childCategory?.categoryNameTrans
        : childCategory?.name,
      url: `/product/${childCategory?.categorySlug}`,
    },
    {
      text: matchingChild?.categoryNameTrans
        ? matchingChild?.categoryNameTrans
        : matchingChild?.name,
      url: `/product/${matchingChild?.categorySlug}`,
    },
    {
      text: selectedCategory?.categoryNameTrans
        ? selectedCategory?.categoryNameTrans
        : selectedCategory?.name,
      url: "javascript:void(0)",
    },
  ];

  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // smooth scrolling animation
    });
    let limit = 10;
    let offset = Math.ceil((data - 1) * limit);
    // setOffset(offset)
    dispatch(offsetCount(offset));
    setCurrentPage(data);
    // router.push(`/product/${params?.slug}?offset=${offset}`)
    setTriiger(trigger + 1);
    setLoader(false);
  };
  const nPages1 = Math.ceil(totCount != undefined && totCount / 10);
  useEffect(() => {
    setCurrentPage(offset / 10 + 1);
  }, []);

  let obj = {};

  const handleSort = (data) => {
    setLoader(false);
    setSortname(data?.name);
    setTriggers(triggers + 1);
    if (data?.id == 1) {
      obj.price = "ASC";
      obj.latestArrival = 0;
    }
    if (data?.id == 2) {
      obj.price = "DESC";
      obj.latestArrival = 0;
    }
    if (data?.id == 3) {
      obj.price = "";
      obj.latestArrival = 1;
    }
    setorder(obj);
  };

  useEffect(() => {
    if (triggers != 0) {
      Productfilters(
        params?.slug,
        attibutefilter,
        setProductdetails,
        setLoader,
        offset,
        variantfilter,
        order,
        customHighPrice == "" ? mPrices : customHighPrice,
        customLowPrice,
        dispatch,
        categoryname
      );
      ProductfiltersCount(
        params?.slug,
        attibutefilter,
        setTotConunt,
        variantfilter,
        order,
        customHighPrice == "" ? mPrices : customHighPrice,
        customLowPrice
      );
    }
  }, [triggers, customLowPrice, customHighPrice]);

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb
          breadCrumb={
            selectedCategory?.parentInt == 0
              ? breadCrumb
              : childCategory !== undefined
              ? subbreadCrumbs
              : breadCrumbs
          }
        />
        <div className="gap-x-4 grid grid-cols-[288px_1fr] max-md:grid-cols-1 mx-auto mt-4 pb-6">
          <Productfilter
            selectedval={selectedval}
            handleReset={handleReset}
            loaders={loaders}
            mPrices={mPrices}
            anyPrice={anyPrice}
            setAnyPrice={setAnyPrice}
            customPrice={customPrice}
            setCustomPrice={setCustomPrice}
            customLowPrice={customLowPrice}
            setCustomLowPrice={setCustomLowPrice}
            customHighPrice={customHighPrice}
            setCustomHighPrice={setCustomHighPrice}
            params={params}
            handleSort={handleSort}
            sortname={sortname}
            selectedCategory={selectedCategory}
            pagecount={totCount}
            offset={offset}
          />
          <Productlist
            productdetails={productdetails}
            loader={loader}
            filterdetail={filterdetail}
            selectedval={selectedval}
            headfil={headfil}
            handleRemove={handleRemove}
            params={params}
            pagecount={totCount}
            nPages1={nPages1}
            currentPage={currentPage}
            onPageChange={onPageChange}
            token={token}
          />
        </div>
      </div>

      <Contactus token={token} />
    </>
  );
}
