"use client";

// React and Next
import { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";
import Link from "next/link";

//3rd Party
import {
  Accordion,
  AccordionContent,
  AccordionPanel,
  AccordionTitle,
} from "flowbite-react";
import {
  Popover,
  PopoverButton,
  PopoverPanel,
  Transition,
} from "@headlessui/react";
//Redux
import { useDispatch } from "react-redux";
import {
  breadcrumallcategory,
  breadcrumb,
  categoryname,
  productslug,
  subproduct,
} from "@/redux/slices/userSlice";

//Utils
import { GetAddonList } from "@/utilities/localStorage";
import { Cookiescategory } from "@/utilities/cookies";
import { sortCategories } from "@/utilities/globalFunctions";

//Component
import ConnectPlugin from "@/Components/connectPlugins";
import ProductFiltersSkeleton from "./skeleton";

export default function Productfilter({
  selectedval,
  handleReset,
  loaders,
  mPrices,
  anyPrice,
  setAnyPrice,
  customPrice,
  setCustomPrice,
  customLowPrice,
  setCustomLowPrice,
  customHighPrice,
  setCustomHighPrice,
  params,
  sortname,
  handleSort,
  selectedCategory,
  pagecount,
  offset,
}) {
  const { t } = useTranslation();
  const sortedChildren = sortCategories(selectedCategory?.children || []);
  const dispatch = useDispatch();
  const addonList = GetAddonList();
    const [renderPanel, setRenderPanel] = useState(false);

  const handleprice = (e, btnChk) => {
    if (btnChk == "radio1") {
      setAnyPrice(e.target.checked);
      setCustomPrice(false);
      setCustomLowPrice(false);
      setCustomHighPrice(false);
    } else {
      setAnyPrice(false);
      setCustomPrice(e.target.checked);
    }
  };

  const handleCustomLowInput = (e, inputChk) => {
    setCustomLowPrice(e.target.value);
  };
  const handleCustomHighInput = (e) => {
    const inValue = Number(e.target.value);
    const maxVale = Number(mPrices);
    if (inValue <= maxVale) {
      setCustomHighPrice(e.target.value);
    } else {
      setCustomHighPrice(mPrices);
    }
  };
  const hadleKeyInput = () => {
    setCustomPrice(true);
    setAnyPrice(false);
  };

  const sortFilter = [
    {
      id: 0,
      name: t("priceFilter.All"),
      number: -1,
    },
    {
      id: 1,
      name: t("priceFilter.lowPr"),
      number: 0,
    },
    {
      id: 2,
      name: t("priceFilter.highPr"),
      number: 0,
    },
    {
      id: 3,
      name: t("priceFilter.mostPr"),
      number: 1,
    },
  ];

  const handleCategoryNames = (name, slug) => {
    dispatch(categoryname(name));
    dispatch(breadcrumb(false));
    dispatch(productslug(slug));
    dispatch(breadcrumallcategory(false));
    dispatch(subproduct(false));
    Cookiescategory(name);
  };

  return (
    <>
      <div className="flex flex-col col-span-2 max-md:col-span-1 mb-[20px]">
        <div>
          <div className="float-left">
            <h3 className="mb-[3px] font-black text-[#323332] text-[18px] leading-[22px]">
              {selectedCategory?.categoryNameTrans
                ? selectedCategory?.categoryNameTrans
                : selectedCategory?.name}
            </h3>
            <p className="font-medium text-[#303030] text-[14px] leading-[17px]">
              {t("productlist.showing")} {offset}-
              {offset + 10 > pagecount ? pagecount : offset + 10}{" "}
              {t("productlist.outOf")} {pagecount}{" "}
              {t("productlist.products")}
            </p>
          </div>
          <div className="float-right">
            <Popover
              className={`relative w-full group z-10 mb-[16px] ${
                renderPanel && "shadow-[0px_2px_30px_#00000024]"
              }`}
            >
              <>
                <PopoverButton
                  className="flex justify-between items-center gap-2 bg-white p-[8px] focus-visible:outline-none w-full min-w-[172px]"
                  onBlur={() => setRenderPanel(false)}
                  onClick={() => {
                    setRenderPanel(!renderPanel);
                  }}
                >
                  <p className="font-normal text-[#999999] text-[14px] leading-[17px]">
                    {t("productlist.sortBy")}
                    <span className="ml-2 font-normal text-[#323332] text-[14px] leading-4">
                      {sortname}
                    </span>
                  </p>{" "}
                  <img src="/img/right.svg" alt="right" />
                </PopoverButton>

                <Transition
                  as={Fragment}
                  enter="transition ease-out duration-200"
                  enterFrom="opacity-0 translate-y-1"
                  enterTo="opacity-100 translate-y-0"
                  leave="transition ease-in duration-150"
                  leaveFrom="opacity-100 translate-y-0"
                  leaveTo="opacity-0 translate-y-1"
                >
                  <PopoverPanel
                    className="top-[100%] right-0 z-10 absolute bg-white shadow-[2px_30px_30px_#00000024] w-full overflow-hidden"
                    onBlur={() => setRenderPanel(false)}
                  >
                    {({ close }) => (
                      <div className="py-[0px]">
                        {sortFilter?.map((data, index) => (
                          <div
                            key={index}
                            className="group relative flex justify-between items-center hover:bg-gray-50 p-[10px_8px] leading-6 cursor-pointer"
                            onClick={(e) => {
                              handleSort(data);
                              setRenderPanel(true);
                              close();
                            }}
                          >
                            <p className="font-normal text-[#323332] text-[13px] leading-4">
                              {data?.name}
                            </p>
                            {sortname == data?.name && (
                              <img src="/img/sortByCheck.svg" alt="Check" />
                            )}
                          </div>
                        ))}
                      </div>
                    )}
                  </PopoverPanel>
                </Transition>
              </>
            </Popover>
          </div>
        </div>
      </div>

      <div className="hidden md:block rtl:ml-4">
        <Accordion className="flex flex-col mb-4 bg-white border-0 group drop-shadow">
          <AccordionPanel>
            <AccordionTitle className="px-4 py-3 pr-3 mb-0 text-sm font-medium bg-white border-0 border-b-0 rounded-md hover:bg-white focus:ring-0 text-black-300">
              {t("myprofile.categories")}
            </AccordionTitle>
            <AccordionContent className="px-2 py-4 pt-0 bg-white border-0 border-t-0 rounded-b-lg border-x-0">
              {/* Category */}
              <div className={"flex flex-col space-y-2 items-start"}>
                <h3 className="px-2 text-sm font-semibold text-black-300">
                  {selectedCategory?.categoryNameTrans
                    ? selectedCategory?.categoryNameTrans
                    : selectedCategory?.name}
                </h3>
                <div className="flex flex-col items-start w-full px-4">
                  {sortedChildren?.map((childCategory, index) => (
                    <Link
                      key={index}
                      href={`/product/${childCategory?.categorySlug}`}
                      className="relative w-full text-sm font-normal cursor-pointer text-black-300 style-5 sub"
                      onClick={() =>
                        handleCategoryNames(
                          childCategory?.name,
                          childCategory?.categorySlug
                        )
                      }
                    >
                      {childCategory?.categoryNameTrans
                        ? childCategory?.categoryNameTrans
                        : childCategory?.name}
                    </Link>
                  ))}
                </div>
              </div>
            </AccordionContent>
          </AccordionPanel>
        </Accordion>

        <div className="p-[12px] border border-[#EAEAEA] border-solid rounded-[5px]">
          <div className="flex justify-between items-start mb-[16px] pb-[12px] border-[#EAEAEA] border-b border-solid rounded-[5px] overflow-hidden">
            <div>
              <h3 className="mb-[3px] font-semibold text-[#303030] text-[14px] leading-[17px]">
                {t("productlist.Filter")}
              </h3>
              <p className="font-normal text-[#999999] text-[10px] leading-[12px]">
                {pagecount} {t("seller.products")}
              </p>
            </div>
            {((selectedval && selectedval?.length != 0) ||
              customLowPrice != "" ||
              customHighPrice != "") && (
              <a
                role="button"
                className="flex justify-end items-center gap-[5px] text-link text-xs cursor-pointer"
                onClick={handleReset}
              >
                {" "}
                {t("myprofile.ResetAll")}{" "}
                <img src="/img/close-blue.svg" alt="close-blue" />{" "}
              </a>
            )}
          </div>

          {/* {loaders == false && <ProductFiltersSkeleton />} */}

          {addonList &&
            addonList?.["product-attribute"] &&
            addonList?.["product-attribute"]?.status == 1 &&
            ConnectPlugin.AttributeFilter && (
              <ConnectPlugin.AttributeFilter params={params?.slug} />
            )}

          {addonList &&
            addonList?.["product-variants"] &&
            addonList?.["product-variants"]?.status == 1 &&
            ConnectPlugin.VariantFilter && (
              <ConnectPlugin.VariantFilter params={params?.slug} />
            )}

          <Accordion className="shadow-none mb-[16px] [&_button]:!mb-0 pb-[16px] border-[#EAEAEA] border-0 border-b-[1px] border-solid rounded-none">
            <AccordionPanel>
              <AccordionTitle className='flex items-center bg-white hover:bg-[bg-white] mb-[16px] p-0 [&~[data-testid="flowbite-accordion-content"]]:!border-t-0 focus:ring-0'>
                <span className="font-semibold text-[#303030] text-[14px] leading-[17px]">
                  {t("productcompare.Price")}
                </span>
              </AccordionTitle>
              <AccordionContent className="mt-[12px] p-0">
                <div className="block">
                  <div className="flex justify-start items-start space-x-[8px] mb-[12px] last-of-type:mb-0 rounded-[0.1875rem] cursor-pointer">
                    <div className="min-w-[15px]">
                      <input
                        id="radio1"
                        checked={anyPrice}
                        onChange={(e) => {}}
                        name="radio1"
                        type="radio"
                        className="peer top-[0.025rem] relative bg-white checked:!bg-[#000000] bg-contain bg-no-repeat checked:bg-none bg-center m-0 border !border-white checked:!border-white checked:border-[0.1875rem] rounded-full outline checked:outline outline-[#CCCCCC] outline-1 checked:!outline-[#000000] checked:outline-[2px] !outline-offset-0 focus:ring-0 w-[15px] checked:w-[13px] min-w-[15px] checked:min-w-[13px] h-[15px] checked:h-[13px] align-top appearance-none cursor-pointer print-color-auto"
                        onClick={(e) => handleprice(e, "radio1")}
                      />
                    </div>
                    <div className="top-[1px] relative w-full text-sm leading-6">
                      <label
                        htmlFor={"radio1"}
                        onClick={(e) => handleprice(e, "radio1")}
                        className="flex justify-between items-center font-normal text-[#333333] text-sm leading-[17px] cursor-pointer"
                      >
                        {t("priceFilter.anyPrice")}
                      </label>
                    </div>
                  </div>
                  <div className="flex justify-start items-start space-x-[8px] mb-[12px] last-of-type:mb-0 rounded-[0.1875rem] cursor-pointer">
                    <div className="flex items-center h-6 shrink-0">
                      <div className="min-w-[15px]">
                        <input
                          id="radio2"
                          checked={customPrice}
                          onChange={(e) => {}}
                          name="radio2"
                          type="radio"
                          className="peer top-[0.025rem] relative bg-white checked:!bg-[#000000] bg-contain bg-no-repeat checked:bg-none bg-center m-0 border !border-white checked:!border-white checked:border-[0.1875rem] rounded-full outline checked:outline outline-[#CCCCCC] outline-1 checked:!outline-[#000000] checked:outline-[2px] !outline-offset-0 focus:ring-0 w-[15px] checked:w-[13px] min-w-[15px] checked:min-w-[13px] h-[15px] checked:h-[13px] align-top appearance-none cursor-pointer print-color-auto"
                          onClick={(e) => handleprice(e, "radio2")}
                        />
                      </div>
                    </div>
                    <div className="top-[2px] relative flex flex-col space-y-[5px]">
                      <div className="w-full text-sm leading-6">
                        <label
                          htmlFor={"radio2"}
                          onClick={(e) => handleprice(e, "radio2")}
                          className="flex items-center justify-between text-sm font-normal leading-none cursor-pointer text-black-300"
                        >
                          {t("priceFilter.custom")}
                        </label>
                      </div>
                      <div className="flex items-center space-x-2.5">
                        <input
                          onChange={(e) => handleCustomLowInput(e)}
                          onMouseDown={() => hadleKeyInput()}
                          value={customLowPrice}
                          type="number"
                          onWheel={(e) => e.target.blur()}
                          className="p-2.5 border border-[#CCCCCC] focus:border-[#CCCCCC] rounded focus:ring-0 w-full h-[37px] font-normal text-black-300 text-sm"
                          placeholder={t("priceFilter.low")}
                        />
                        <p className="font-normal text-[#BCBCBC] text-sm">
                          {t("priceFilter.to")}
                        </p>
                        <input
                          onChange={(e) => handleCustomHighInput(e, "high")}
                          onMouseDown={() => hadleKeyInput()}
                          value={customHighPrice}
                          type="number"
                          onWheel={(e) => e.target.blur()}
                          className="p-2.5 border border-[#CCCCCC] focus:border-[#CCCCCC] rounded focus:ring-0 w-full h-[37px] font-normal text-black-300 text-sm"
                          placeholder={t("priceFilter.high")}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </AccordionContent>
            </AccordionPanel>
          </Accordion>
        </div>
      </div>
    </>
  );
}
